import React from 'react';
import { CurrentPathBadge } from './CurrentPathBadge';
import { 
// Route,
Link, useRouteMatch, useHistory, } from 'react-router-dom';
import { EditProfileDialog } from './EditProfileDialog';
import { notification, 
// UseDialog,
useDialog, } from 'dialogic-react';
import { saveConfirmationProps } from './SaveConfirmation';
export const ProfilePage = () => {
    const match = useRouteMatch();
    const history = useHistory();
    const dialogPath = `${match.url}/edit`;
    const dialogReturnPath = match.url;
    const matchDialogPath = useRouteMatch(dialogPath);
    useDialog({
        isShow: matchDialogPath ? matchDialogPath.isExact : false,
        props: {
            dialogic: {
                component: EditProfileDialog,
                className: 'dialog',
                queued: true,
            },
            title: 'Update your e-mail',
            email: 'allan@company.com',
            onSave: (email) => {
                console.log('onSave:', email);
                history.push(dialogReturnPath);
                notification.show(saveConfirmationProps);
            },
            onCancel: () => {
                console.log('onCancel');
                history.push(dialogReturnPath);
            },
        },
    });
    return (React.createElement(React.Fragment, null,
        React.createElement("h1", { className: "title" }, "Profile"),
        React.createElement(CurrentPathBadge, null),
        React.createElement("div", { className: "buttons" },
            React.createElement(Link, { className: "button", to: "/" }, "Go to Home"),
            React.createElement(Link, { className: "button is-link", to: dialogPath }, "Edit Profile"))));
};
